import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import Papa from 'papaparse';

import io from 'socket.io-client';
//import axios from 'axios-jsonp-pro'
import PanelHeader from '../components/MainEpicHeader'
import { View, Panel, Banner, Snackbar, Avatar, ConfigProvider, SimpleCell } from '@vkontakte/vkui';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';

const images = { AppleMusic: require('../img/AppleMusic.svg'), Spotify: require('../img/Spotify.svg'), Yandex: require('../img/Yandex.svg') }

export default class Home extends React.Component {
	constructor(props) {
		super(props);
	
		this.state = {
		  activePanel: 'home',
		  history: ['home'],
		  snackbar: null,
		  vk_platform: 'browser',
		  songs: [],
		  count: 0
		}
	  }

	  goBack = () => {
		var history = this.state.history;

		if (this.state.activePanel === 'home') {
			bridge.send('VKWebAppDisableSwipeBack');
		}
		
		if ( history.length > 1 ) {
		  history.pop()
		  if (history[history.length - 1] === 'home') {
			this.props.setTabbar(true)
			document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
			document.documentElement.style.setProperty('--bottom', 'calc(var(--tabbar_height) + var(--safe-area-inset-bottom))');
		  }
		  this.setState({ activePanel: history[history.length - 1] })
		}
	  }
	  goToPage( name ) {
		window.history.pushState( { panel: name }, name );
		if (this.state.activePanel === 'home') {
			bridge.send('VKWebAppEnableSwipeBack');
			this.props.setTabbar(null)
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		}
		this.setState({ 
			activePanel: name,
			history: [...this.state.history, name]
		})
	  }

	  openSuccess () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundColor: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Отправили!
		  </Snackbar>
		});
	  }
	  openError () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #fc7373, #cb5e5a)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Ошибка
		  </Snackbar>
		});
	  }

	  componentDidMount() {
		window.addEventListener('popstate', () => this.goBack());
		if (window.location.search) {
			var vk_platform = window.location.search.match(/vk_platform=([a-z_]+)/)
			this.setState({vk_platform: vk_platform[1].toString()})
		}
	  }

render () {
	return (
	<Panel id={this.id}>
	 <ConfigProvider
	 scheme={document.body.getAttribute('scheme') ? document.body.getAttribute('scheme') : 'bright_light'}
	 >
	  <View activePanel={this.state.activePanel} history={this.state.history} onSwipeBack={this.goBack}>
	 	<Panel id="home">
          <PanelHeader>Главная</PanelHeader>
		  <Banner
		  	id='Apple'
		  	mode="image"
		  	background={
				<div
				  style={{
					backgroundColor: '#ff4e5c'
				  }}
				/>
			  }
            before={<Avatar size={76} mode="image" src={images.AppleMusic} />}
            header="Apple Music"
            subheader="Import music from File."
            onClick={()=>{
				const input = document.createElement('input');
				input.type = 'file'
				input.accept = '.txt'
				//this.props.setPopout(<ScreenSpinner />)
				input.addEventListener("change", (e) => {
					let reader = new FileReader();
					var text, playlist = [];

					reader.readAsText(e.target.files[0], 'UTF-8');
					reader.onload = (evt) => {
						text = evt.target.result;

						let parsed = Papa.parse(text, {
							header: true
						  });
						if (parsed.errors.length && !parsed.data.length) {
							this.openError()
						}
						parsed.data.pop();
						for (let index = 0; parsed.data.length > index; index++) {
							const
							  track = parsed.data[index],
							  keys = Object.keys(track);
							if (!keys.includes('Name') || !keys.includes('Artist') || !keys.includes('Album')) {
								this.openError()
							}
							playlist.push({
							  index,
							  name           : track.Name,
							  artist         : track.Artist,
							  album          : track.Album
							});
						  }
					this.setState({count: playlist.length}, () => this.goToPage('transfer'));
					const socket = io('https://music.dan0102dan.ru');
					socket.on('connect', () => {
						socket.emit('AppleMusic', playlist);
						socket.on('message', (data) => {
							this.setState((state) => { 
								return {songs: state.songs.concat(data)} 
							});
							console.log(this.state.songs);
						  });
  					});
				  }
				}, false);
				input.click();
			}}
          />
		  <Banner
		  	id='Spotify'
		  	mode="image"
		  	background={
				<div
				  style={{
					backgroundColor: '#36a85d'
				  }}
				/>
			  }
            before={<Avatar size={76} mode="image" src={images.Spotify} />}
            header="Spotify"
            subheader="Import music from Spotify playlist."
            onClick={()=>{
				const input = document.createElement('input');
				input.type = 'file'
				input.addEventListener("change", handleFiles, false);
				input.click();
				function handleFiles() {
					console.log(this.files)
				}
			}}
          />
		  <Banner
		  	id='Yandex'
		  	mode="image"
		  	background={
				<div
				  style={{
					backgroundColor: '#f8db64'
				  }}
				/>
			  }
            before={<Avatar size={76} mode="image" src={images.Yandex} />}
            header="Yandex Music"
			subheader="Import music from Yandex playlist."
            onClick={()=>{
				const input = document.createElement('input');
				input.type = 'file'
				input.addEventListener("change", handleFiles, false);
				input.click();
				function handleFiles() {
					console.log(this.files)
				}
			}}
          />
	  	{this.state.snackbar}
      </Panel>

	  <Panel id="transfer">
          <PanelHeader>Найдено {this.state.songs.length}/{this.state.count}</PanelHeader>
		   {this.state.songs ? this.state.songs.map(song => <SimpleCell
		   className="SongList"
		   key={song.id}
		   before={<Avatar mode="image" src={song.image} />}
		   description={song.artist}>
			   {song.title}
			</SimpleCell>)
			: null
			}
	  	{this.state.snackbar}
      </Panel>
      </View>
	 </ConfigProvider>
	</Panel>
	)
 }
}